	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"Relief!","date":"23 Sep 2000","slug":"/2000-09-23-relief/","tags":"Y2000"},"html":"<p>A big load off my shoulders today... the new company website is online, nearly two months late and just two working days before we fly to the US! I would have preferred that it be up running for a month before skipping off, but 48 hours is a good burn-in time, right?</p>\n<p>In the meantime, Mama's done the lion's share of planning and preparation for our trip. We've done plenty o trips between Europe and the US, but adding Christopher to the equation will make things roughly twice as complicated, I'm guessing.</p>\n<p>We fly first to Phoenix for a week, then to Minnesota for 2 weeks. We'll be visiting friends and family... no time to play tourist!</p>"},"next":{"fields":{"title":"Olympic Shootout","date":"17 Sep 2000","slug":"/2000-09-17-olympic-shootout/"}},"previous":{"fields":{"title":"Behind the Curtain","date":"24 Sep 2000","slug":"/2000-09-24-behind-the-curtain/"}}},{"node":{"fields":{"title":"Olympic Shootout","date":"17 Sep 2000","slug":"/2000-09-17-olympic-shootout/","tags":"Y2000"},"html":"<p>To bad we didn't do the weblog shootout yesterday... we would have had a lot of fun pictures. We're having the patio extended behind the carport, and a mini-backhoe rumbled through our backyard to haul away the dirt. We also had bath day with Christopher, attended a baby-flea-market in Holm-Seppensen (we spent DM 10 for a stocking cap, a bottle warmer, and a Fisher-Price radio), we had a very pregnant visitor, and I started reorganizing my desk, moving the secondary PCs to the shelves behind me. Today all we have planned is swimming at the Salztherme (salt spa) in Lüneburg.</p>"},"next":{"fields":{"title":"Busy busy","date":"12 Sep 2000","slug":"/2000-09-12-busy-busy/"}},"previous":{"fields":{"title":"Relief!","date":"23 Sep 2000","slug":"/2000-09-23-relief/"}}},{"node":{"fields":{"title":"Busy busy","date":"12 Sep 2000","slug":"/2000-09-12-busy-busy/","tags":"Y2000"},"html":"<p>Lots going on, lots to think about this week. No <a href=\"http://brdbrain.editthispage.com/2000/09/10\">time to update the weblog</a>. New <a href=\"http://www.abendblatt.de/bin/ha/set_frame/set_frame.cgi?seiten_url=/contents/ha/news/wirtschaft/html/090900/219AUFM16.HTM\">CEO</a> at work, and no idea what it all means. Big decisions? We'll see.</p>\n<p>But hey, we'll be taking pictures for the big <a href=\"http://array.editthispage.com/24hours\">weblog shootout</a> this coming Sunday and Monday. Christopher nonstop. But will it be as much fun as the German weblog rally? We'll see.</p>\n<p><a href=\"http://array.editthispage.com/24hours\"><img src=\"http://www.arrayweb.com/btc/english.jpg\" /></a></p>"},"next":{"fields":{"title":"Happy Anniversary to Us","date":"06 Sep 2000","slug":"/2000-09-06-happy-anniversary-to-us/"}},"previous":{"fields":{"title":"Olympic Shootout","date":"17 Sep 2000","slug":"/2000-09-17-olympic-shootout/"}}}],"pathPrefix":"","first":false,"last":false,"index":1042,"pageCount":1101}}

